import {Logger} from "@ceros/markup-common/frontend/logger";

export function clearAlarm(name: string): Promise<void> {
    return new Promise((resolve: () => void): void => {
        chrome.alarms.clear(name, resolve);
    });
}

export async function getActiveTab(): Promise<chrome.tabs.Tab> {
    const tabs: chrome.tabs.Tab[] = await tabQuery({active: true, currentWindow: true});
    switch (tabs.length) {
        case 0:
            throw new Error("Query for active tab returned no tabs");
        case 1:
            return tabs[0];
        default:
            Logger.warn(`Query for active tab returned ${tabs.length}`);
            return tabs[0];
    }
}

export function tabQuery(queryInfo: chrome.tabs.QueryInfo): Promise<chrome.tabs.Tab[]> {
    return new Promise((resolve: (tabs: chrome.tabs.Tab[]) => void): void => {
        chrome.tabs.query(queryInfo, resolve);
    });
}

export function sendMessage(message: any): Promise<any> {
    return new Promise((resolve: (result: any) => void): void => {
        chrome.runtime.sendMessage(message, resolve);
    });
}

export function sendTabMessage(tabId: number, message: any, options?: chrome.tabs.MessageSendOptions): Promise<any> {
    return new Promise((resolve: (result: any) => void): void => {
        if (options) {
            chrome.tabs.sendMessage(tabId, message, options, resolve);
        } else {
            chrome.tabs.sendMessage(tabId, message, resolve);
        }
    });
}

export function captureVisibleTab(): Promise<string> {
    return new Promise((resolve: (dataUrl: string) => void): void => {
        chrome.tabs.captureVisibleTab(resolve);
    });
}

export function getAllChromeCookies(details: chrome.cookies.GetAllDetails = {}): Promise<chrome.cookies.Cookie[]> {
    return new Promise((resolve: (chromeCookies: chrome.cookies.Cookie[]) => void): void => {
        chrome.cookies.getAll(details, resolve);
    });
}

export function setChromeCookie(details: chrome.cookies.SetDetails): Promise<void> {
    return new Promise((resolve: () => void): void => {
        chrome.cookies.set(details, resolve);
    });
}

export function removeChromeCookie(details: chrome.cookies.Details): Promise<void> {
    return new Promise((resolve: () => void): void => {
        chrome.cookies.remove(details, resolve);
    });
}
